body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.navbar {
    position: fixed;
    width: 100%;
    box-shadow: 0 14px 28px rgba(0,0,0,.25), 0 10px 10px rgba(0,0,0,.22);
    z-index: 99;
    height: 85px;
    background-color: #ad2a91;
    color: #ffffff;
}

.navbar a {
    text-decoration: none;
    color: #ffffff;
    font-weight: 800;
}

.navbar-text {
    margin-right: 70px;
}

#description {
    margin: 250px;
}

header {
    height: 450px;
	width: 100%;
	margin: 0px;
    display: table;
    /* opacity: 0.8; */
    min-height: 400px;
    padding: 180px 20px;
    padding-bottom: 100px;
    text-align: center;
    color: #ffffff;	    
    background-image: url(https://cdn.mos.cms.futurecdn.net/djeRcVyAHcZC8xpYPkEvw.jpg);
    background-size: cover;
	background-attachment: fixed;
}




.footer {
    position: relative;
    bottom: 0;
    width: 100%;
    height: 70px;
    line-height: 60px;
    display: block;
    overflow: hidden;    
    left: 0;
    right: 0;
    padding: 8px;
    font-weight: 600;
    color: #ffffff;
    background: #ad2a91;
}

#logo {
    height: 30px;
    margin-left: 10px;
    margin-bottom: 5px;
}
.wrapper {
    min-height: 100%;
    padding-bottom: 100px;
  }
  

.card > .img-container {
  height: 100%;
  overflow: hidden;
  text-align: center;
  background: #ffffff; 
} 

.card > .img-container > img {
  height: 200px;
  width: 160px;
}
  
.card > .content {
  padding-left: 1rem;
  padding-right: 1rem;
  font-size: 15px;
}

.card {
  height: 220px;
  width: 200px;
  margin: 20px 25px 20px 40px;
  border: 5px solid #e6e6e6;
  border-radius: 3px;
  box-shadow: 0 3px 6px rgba(0,0,0,.16), 0 3px 6px rgba(0,0,0,.23);
  cursor: pointer;
  transition: all .2s ease-in-out;
}

.card:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1); 
  cursor: pointer;
}
