
.card > .img-container {
  height: 100%;
  overflow: hidden;
  text-align: center;
  background: #ffffff; 
} 

.card > .img-container > img {
  height: 200px;
  width: 160px;
}
  
.card > .content {
  padding-left: 1rem;
  padding-right: 1rem;
  font-size: 15px;
}

.card {
  height: 220px;
  width: 200px;
  margin: 20px 25px 20px 40px;
  border: 5px solid #e6e6e6;
  border-radius: 3px;
  -webkit-box-shadow: 0 3px 6px rgba(0,0,0,.16), 0 3px 6px rgba(0,0,0,.23);
  box-shadow: 0 3px 6px rgba(0,0,0,.16), 0 3px 6px rgba(0,0,0,.23);
  cursor: pointer;
  transition: all .2s ease-in-out;
}

.card:hover {
  transform: scale(1.1); 
  cursor: pointer;
}